<template>
    <div id="secure">
        <h1>Secure Area</h1>
        <p>
            This is a secure area
        </p>
        <router-link to="/secure/hello">hello</router-link>
        <router-link to="/secure/home">home</router-link>
        <router-view/>
    </div>
</template>

<script>
import HelloWorld from '@/components/HelloWorld'
import Second from '@/components/secondcomp'
    export default {
        name: 'Secure',
        components:{
          HelloWorld,Second
        },
        data() {
            return {};
        }
    }
</script>

<style scoped>
    #secure {
        background-color: #FFFFFF;
        border: 1px solid #CCCCCC;
        padding: 20px;
        margin-top: 10px;
    }
</style>



