import Vue from 'vue'
import Router from 'vue-router'
import LoginComponent from "./views/login.vue"
import SecureComponent from "./views/secure.vue"
import HelloWorld from '@/components/HelloWorld.vue'
import Second from '@/components/secondcomp.vue'

Vue.use(Router)

export default new Router({
  mode:'history',
    routes: [
        {
            path: '/',
            redirect: {
                name: "login"
            }
        },
        {
            path: "/login",
            name: "login",
            component: LoginComponent
        },
        {
            path: "/secure",
            name: "secure",
            component: SecureComponent,
                children: [
            {
              // when /secure/hello is matched
              path: 'hello',
              name: "hello",
              component: HelloWorld
            },
            {
              // when /secure/home is matched
              path: 'home',
              name:'home',
              component: Second
            }
          ]
      }
    ]
})



